<template>
  <div class="login2">
    <Button class="hy_btn" style="margin-right: 30px;" @click="href1()">门店系统</Button>
    <Button class="hy_btn" @click="href2()">管理系统</Button>
  </div>
</template>

<script>
  export default{
    name: 'login2',
    methods:{
      href1(){},
      href2(){
        this.$router.push({name: ''})
      }
    }
  }
</script>
<style scoped>
  .login2{
    width: 500px;
    margin:200px auto;
    border: 1px solid #000;
    text-align: center;
  }
</style>



